.list {
  margin: 1.5rem 0;
  margin-left: 2rem;

  &--nested {
    margin-top: 0.1rem;
    margin-bottom: 0.1rem;
  }

  &--alpha {
    list-style-type: lower-alpha;
  }
  
  &__item {
    line-height: 1.5;
    margin: 0.1rem 0;
    color: $black;
    font-size: $extrathin;
    font-family: $avertaLight;
    font-weight: bold;
    padding-bottom: 4px;
  }
}
